  background-color: #020303;
  overflow-x: hidden;
  font-size: 50px;
  font-size: calc(16px + (50 - 16) * ((100vw - 600px) / (1000 - 600)));
  -webkit-overflow-scrolling: touch;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@media (max-width: 600px) {
  body {
    font-size: 16px;
  }
}

@media (min-width: 1000px) {
  body {
    font-size: 50px;
  }
}

img {
  display: block;
  width: 100%;
}

a {
  text-decoration: none;
}

.logo {
  width: 120px;
  position: absolute;
  top: 25px;
  left: 25px;
  z-index: 99;
}

.text-accent {
  color: #A00A90;
}

.block-bg {
  display: inline-block;
  padding: 0 9px 0 5px;
}

.block-bg--purple {
  background-color: #A00A90;
}

.block-bg--yellow {
  background-color: #ffe100;
}

.block-bg--red {
  background-color: #f90000;
}

.block-bg--blue {
  background-color: #445bf7;
}

.block-bg--white {
  background-color: #fff;
}

.block-bg--animate {
  margin-bottom: -12px;
  overflow: hidden;
  background-color: transparent;
  position: relative;
}

.block-bg--animate::before {
  content: '';
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #A00A90;
  transform: translateX(-100%);
  transition: 3s;
}

.block-bg--animate::after {
  content: attr(data-num);
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  color: #fff;
  z-index: 999;
}

.block-bg--animate-1::before {
  transition-delay: 2.5s;
}

.block-bg--animate-2::before {
  transition-delay: 3s;
}

.block-bg--animate-3::before {
  transition-delay: 3.5s;
}

.section {
  text-transform: uppercase;
  background-position: right bottom;
  background-repeat: no-repeat;
  background-size: contain;
  overflow: hidden;
}

.section.active .subtitle {
  opacity: 1;
  transform: translateY(0);
}

.section.active .title__row, .section.active .title__sub {
  opacity: 1;
  transform: translateX(0);
}

.section.active .desc__row, .section.active .video-link, .section.active .video-link span {
  opacity: 1;
  transform: translateX(0);
}

.section.active .block-bg--animate::before {
  transform: translateX(0);
}

.section.prc-10 {
  background-position: 10% bottom;
}

@media only screen and (max-width: 1400px) {
  .section.prc-10 {
    background-position: left bottom;
  }
}

.section--reverse {
  background-position: left bottom;
  margin-left: auto;
}

.section--reverse .title__row {
  transform: translateX(100%);
}

.section--reverse .content {
  margin-right: 13%;
  text-align: right;
  align-items: flex-end;
}

@media only screen and (max-width: 1400px) {
  .section--reverse .content {
    margin-right: 15%;
  }
}

.section--reverse .video-link__icon {
  right: auto;
  left: 0;
}

.content {
  display: flex;
  flex-direction: column;
  align-items: baseline;
  margin-left: 13%;
}

@media only screen and (max-width: 1400px) {
  .content {
    margin-left: 15%;
  }
}

.subtitle {
  margin-bottom: 115px;
  font-weight: bold;
  font-size: 22px;
  transform: translateY(-100%);
  opacity: 0;
  transition: 1s;
  transition-delay: 1s;
}

@media only screen and (max-width: 1400px) {
  .subtitle {
    margin-bottom: 65px;
  }
}

.title {
  margin-bottom: 135px;
  font-size: 75px;
  line-height: 1.2;
  display: inline-flex;
  flex-direction: column;
}

@media only screen and (max-width: 1400px) {
  .title {
    margin-bottom: 90px;
  }
}

.title__sub {
  margin-left: auto;
  margin-bottom: 5px;
  font-size: 28px;
  transform: translateX(100%);
  opacity: 0;
  transition: 1s;
  position: absolute;
  top: -40px;
  right: 0;
}

.title__row {
  line-height: 1;
  position: relative;
  transform: translateX(-100%);
  opacity: 0;
  transition: 1s;
  transition-delay: 1.5s;
}

.title__row:nth-child(2) {
  transition-delay: 2s;
}

.title__row:nth-child(3) {
  transition-delay: 2.5s;
}

.title__row + .title__row {
  margin-top: 10px;
}

.title__row .block-bg--yellow, .title__row .block-bg--red, .title__row .block-bg--blue, .title__row .block-bg--white {
  color: #000;
}

.title small {
  display: block;
  font-size: 50px;
}

.desc {
  text-transform: uppercase;
  font-weight: bold;
  font-size: 33px;
  line-height: 1.4;
}

.desc__row {
  transform: translateY(100%);
  opacity: 0;
  transition: 1s;
  transition-delay: 3s;
}

.desc__row:nth-child(2) {
  transition-delay: 3.5s;
}

.desc__row:nth-child(3) {
  transition-delay: 4s;
}

.desc span {
  display: block;
}

.video-link {
  display: inline-block;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 33px;
  line-height: 1.2;
  color: #fff;
  transform: translateY(100%);
  opacity: 0;
  transition: 1s;
  transition-delay: 3s;
}

.video-link span {
  display: block;
  position: relative;
  transform: translateY(100%);
  opacity: 0;
  transition: 1s;
  transition-delay: 3.5s;
}

.video-link__icon {
  content: '';
  display: block;
  background-image: url("../img/sprite.png");
  width: 42px;
  height: 30px;
  background-position: 0px 0px;
  position: absolute;
  right: 0;
  top: 5px;
  animation-duration: 2.5s;
  animation-delay: 20s;
}
